.vue-touch-keyboard .keyboard{width:100%;margin:0}

.vue-touch-keyboard .keyboard .line{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around}

.vue-touch-keyboard .keyboard .line:not(:last-child){margin-bottom:.5em}

.vue-touch-keyboard .keyboard .key{-webkit-box-flex:40;-ms-flex:40;flex:40;height:2.2em;line-height:2.2em;overflow:hidden;vertical-align:middle;border:1px solid #ccc;color:#333;background-color:#fff;-webkit-box-shadow:0 2px 2px rgba(0,0,0,.6);box-shadow:0 2px 2px rgba(0,0,0,.6);border-radius:.35em;font-size:1.25em;text-align:center;white-space:nowrap;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}

.vue-touch-keyboard .keyboard .key:not(:last-child){margin-right:.5em}

.vue-touch-keyboard .keyboard .key.backspace{background-image:url(data:image/svg+xml;base64,);background-position:50%;background-repeat:no-repeat;background-size:35%}

.vue-touch-keyboard .keyboard .key.half{-webkit-box-flex:20;-ms-flex:20;flex:20}

.vue-touch-keyboard .keyboard .key.control{color:#fff;background-color:#7d7d7d;border-color:#656565}

.vue-touch-keyboard .keyboard .key.featured{color:#fff;background-color:#337ab7;border-color:#2e6da4}

.vue-touch-keyboard .keyboard .key:hover{color:#333;background-color:#d6d6d6;border-color:#adadad}

.vue-touch-keyboard .keyboard .key:active{-webkit-transform:scale(.98);transform:scale(.98);color:#333;background-color:#d4d4d4;border-color:#8c8c8c}

.vue-touch-keyboard .keyboard .key.activated{color:#fff;background-color:#5bc0de;border-color:#46b8da}

.vue-touch-keyboard .keyboard .placeholder{-webkit-box-flex:20;-ms-flex:20;flex:20;height:2.2em;line-height:2.2em}

.vue-touch-keyboard .keyboard .placeholder:not(:last-child){margin-right:.5em}

.vue-touch-keyboard .keyboard:after,.vue-touch-keyboard .keyboard:before{content:"";display:table}

.vue-touch-keyboard .keyboard:after{clear:both}


















































/*
** element ui 的部分css
** 无视即可
*/
.el-input__inner {
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 1;
  outline: 0;
  padding: 0 15px;
  -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
  width: 100%;
}
.el-input {
  position: relative;
  font-size: 14px;
  display: inline-block;
  width: 100%;
  margin-top: 30px;
}
.el-textarea {
  display: inline-block;
  width: 100%;
  vertical-align: bottom;
  font-size: 14px;
  margin-top: 30px;
}
.el-textarea__inner {
  display: block;
  resize: vertical;
  padding: 5px 15px;
  line-height: 1.5;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  font-size: inherit;
  color: #606266;
  background-color: #fff;
  background-image: none;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
}
